<template>
  <p>Please first fetch the user data and then select org from dropdown</p>
  <div>
    <button @click="getLocation">Get Location</button>

    <button @click="fetchGoogleMapsLocation">
      Get Location with Google API
    </button>

    <p>
      Geolocation API accuracy in meters when fetching user location:{{
        geolocationApiAccuracy
      }}
    </p>

    <p>
      Google Maps Geolocation API accuracy in meters when fetching user
      location: {{ googleApiAccuracy }}
    </p>

    <br />
    <p>Latitude {{ latitude }}</p>
    <p>Longitude {{ longitude }}</p>
    <br />

    <p>Select a organization</p>
    <select v-model="selectedOrganization" @change="onOrganizationChange">
      <option v-for="org in organizations" :value="org" :key="org.id">
        {{ org.nameOfOrganization }}
      </option>
    </select>
    <p>Distance to organization {{ distanceToOrganization }}</p>

    <span>
      <p>{{ userMessage }}</p>
    </span>
  </div>
</template>

<script>
import { point, distance } from "@turf/turf";

export default {
  name: "TestingMultiple",
  data() {
    return {
      geolocationApiAccuracy: null,
      googleApiAccuracy: null,
      latitude: "",
      longitude: "",
      selectedOrganization: null,
      distanceToOrganization: null,
      isPunchedIn: false,
      isPunchedOut: false,
      userMessage: "",
      organizations: [
        {
          id: 1,
          nameOfOrganization: "Convenience, Dingli",
          latitude: 35.861579718553045,
          longitude: 14.384102888618935,
          meters: 200,
          startTime: "2024-02-10T00:15:00Z",
          endTime: "2024-02-10T00:18:00Z",
        },
        {
          id: 2,
          nameOfOrganization: "Barbajean",
          latitude: 35.86092166581468,
          longitude: 14.382550960199088,
          meters: 20,
          startTime: "2024-02-08T11:00:00Z",
          endTime: "2024-02-08T11:03:00Z",
        },
        {
          id: 3,
          nameOfOrganization: "Patrick Prints",
          latitude: 35.870565544942906,
          longitude: 14.44673215440333,
          meters: 20,
          startTime: "2024-02-08T11:00:00Z",
          endTime: "2024-04-08T11:03:00Z",
        },
        {
          id: 4,
          nameOfOrganization: "Master Grocer",
          latitude: 35.87132748428104,
          longitude: 14.446681336282058,
          meters: 20,
          startTime: "2024-02-08T11:00:00Z",
          endTime: "2024-04-08T11:03:00Z",
        },
        {
          id: 5,
          nameOfOrganization: "Lidl Mosta - Corner",
          latitude: 35.91783234865111,
          longitude: 14.420965580177286,
          meters: 20,
          startTime: "2024-02-08T11:00:00Z",
          endTime: "2024-04-08T11:03:00Z",
        },
        {
          id: 6,
          nameOfOrganization: "Pavi Qormi - Sterling",
          latitude: 35.878477960904135,
          longitude: 14.48129070104337,
          meters: 20,
          startTime: "2024-02-08T11:00:00Z",
          endTime: "2024-04-08T11:03:00Z",
        },
        {
          id: 7,
          nameOfOrganization: "Mosta Cake Box",
          latitude: 35.90921735094371,
          longitude: 14.42568909379031,
          meters: 20,
          startTime: "2024-02-08T11:00:00Z",
          endTime: "2024-04-08T11:03:00Z",
        },
        {
          id: 8,
          nameOfOrganization: "Christina's Parents",
          latitude: 35.893723826865234,
          longitude: 14.463990444699068,
          meters: 20,
          startTime: "2024-02-08T11:00:00Z",
          endTime: "2024-04-08T11:03:00Z",
        },
      ],
    };
  },

  methods: {
    getLocation() {
      navigator.geolocation.getCurrentPosition(this.success, this.error, {
        enableHighAccuracy: true, // Request high accuracy location
        timeout: 10000, // Time in milliseconds before the request times out
        maximumAge: 0, // Prevent the use of cached locations
      });
    },

    fetchGoogleMapsLocation() {
      const url = `https://www.googleapis.com/geolocation/v1/geolocate?key=AIzaSyAizlo3Mfwncv2biC86x_U4-B7FV2V2qFg`;

      fetch(url, {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
      })
        .then((response) => {
          if (!response.ok) {
            throw new Error("Network response was not ok");
          }
          return response.json();
        })
        .then((data) => {
          this.latitude = data.location.lat;
          this.longitude = data.location.lng;
          this.googleApiAccuracy = `${data.accuracy} meters`;
        })
        .catch((error) => {
          console.error(
            "Error fetching location from Google Maps Geolocation API:",
            error
          );
        });
    },

    success(pos) {
      const crd = pos.coords;
      this.latitude = crd.latitude;
      this.longitude = crd.longitude;
      this.geolocationApiAccuracy = `${crd.accuracy} meters `;
    },

    error(err) {
      console.warn(`ERROR(${err.code}): ${err.message}`);
    },

    onOrganizationChange() {
      this.isPunchedIn = false;
      this.isPunchedOut = false;
      this.userMessage = "";
      if (this.latitude !== null && this.longitude !== null) {
        this.updateLocation({
          coords: {
            latitude: this.latitude,
            longitude: this.longitude,
          },
        });
      }
    },

    updateLocation(position) {
      this.latitude = position.coords.latitude;
      this.longitude = position.coords.longitude;
      const from = point([this.longitude, this.latitude]);
      const to = point([
        this.selectedOrganization.longitude,
        this.selectedOrganization.latitude,
      ]);
      const options = { units: "meters" };
      this.distanceToOrganization = `${distance(
        from,
        to,
        options
      ).toFixed()} meters`;
      this.isPunchedInEnabled();
      this.isPunchOutEnabled();
    },

    isPunchedInEnabled() {
      const now = new Date();
      const startTime = new Date(this.selectedOrganization?.startTime);

      return (
        this.distanceToOrganization <= this.selectedOrganization?.meters &&
        now >= startTime
      );
    },

    isPunchOutEnabled() {
      const now = new Date();
      const endTime = new Date(this.selectedOrganization?.endTime);
      return (
        this.distanceToOrganization <= this.selectedOrganization?.meters &&
        now >= endTime
      );
    },

    handleCheckIn() {
      this.userMessage = `Punched in at ${this.selectedOrganization.nameOfOrganization}`;
      this.isPunchedIn = true;
    },

    handlePunchOut() {
      this.userMessage = `Punched out at ${this.selectedOrganization.nameOfOrganization}`;
      this.isPunchedOut = true;
    },
  },
};
</script>
